import React, { useState } from "react";


 


const Slot = () => {

  
 const[count, setCount] = useState(0);
//console.log(state);



const IncNum = () =>{
  setCount( count+1 );
  //console.log('clicled' + count++);
}

  return (
    <div>
      <h1>Slot Game</h1>
      <div className="text-center">
        <h3>{count}</h3>
      <button type="button" onClick={IncNum} className="btn btn-danger ">Click Me</button>
      </div>
     
    </div>
  );
};

export default Slot;
